html {
  font-size: 15px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  color: #2B2A2C;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  width: 100%;
  max-width: 1200px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
}
.header {
  padding: 1.2rem 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user {
  display: flex;
}
.user__avatar {
  width: 57px;
  height: 57px;
}
.user__avatar img {
  border-radius: 50%;
}
.user__content {
  padding-left: 9px;
}
.user__name {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  color: #2B2A2C;
  padding-bottom: 3px;
}
.user__prof {
  font-size: 12px;
  opacity: 0.7;
}
.nav {
  display: flex;
  height: 100%;
  align-items: center;
}
.nav__link {
  font-size: 14px;
  color: #2B2A2C;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  margin-left: 30px;
  position: relative;
}
.nav__link:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: transparent;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
  transition: background 0.2s linear;
}
.nav__link:hover:not(.nav__link--btn):after {
  background-color: #2B2A2C;
}
.nav__link--btn {
  padding: 10px 30px;
  border: 2px solid #2B2A2C;
  border-radius: 20px;
  transition: background 0.2s linear, color 0.2s linear, box-shadow 0.2s linear;
}
.nav__link--btn:hover {
  background-color: #2B2A2C;
  color: #fff;
  box-shadow: 2px 2px 10px rgba(43, 42, 44, 0.24);
}
.intro__inner {
  display: flex;
  background: url("/images/symbols.png") right bottom no-repeat;
  border-bottom: 1px solid rgba(106, 105, 107, 0.3);
  padding-top: 103px;
  padding-bottom: 123px;
  position: relative;
}
.intro__subtitle {
  font-size: 32px;
  font-weight: 200;
  color: #2B2A2C;
}
.intro__title {
  margin-bottom: 5px;
  font-size: 45px;
  color: #2B2A2C;
  font-weight: 700;
}
.intro__text {
  font-size: 18px;
  color: #2B2A2C;
  opacity: 0.7;
  margin-bottom: 25px;
}
.intro__content {
  width: 50%;
}
.intro__photo {
  position: absolute;
  bottom: -1px;
  right: 0;
  z-index: 1;
}
.social {
  display: flex;
  margin-bottom: 42px;
}
.social__link {
  margin-right: 10px;
  text-decoration: none;
  transition: transform 0.2s linear;
}
.social__link:hover {
  transform: translateY(-8px);
}
.btn {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  color: #2B2A2C;
  text-decoration: none;
  text-transform: uppercase;
  font-family: inherit;
  line-height: 16px;
  text-align: center;
  background: none;
  cursor: pointer;
  padding: 20px 26px;
  border: 1px solid #6A696B;
  border-radius: 30px;
  margin-right: 10px;
  min-width: 170px;
  transition: background 0.2s linear, color 0.2s linear, box-shadow 0.2s linear;
}
.btn:focus {
  outline: none;
}
.btn:hover {
  background-color: #2B2A2C;
  color: #fff;
  box-shadow: 2px 2px 10px rgba(43, 42, 44, 0.24);
}
.btn + .btn {
  margin-left: 15px;
}

